body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}h1,h2,h3,h4,h5,h6{margin:0}.App{width:100%;height:100%}.modal-container{background-color:#e5e5e5;width:50%;height:50%}.container{width:80%;margin:0 auto}.flex{display:flex}.wrap{flex-wrap:wrap}.col{flex-direction:column}.space-between{justify-content:space-between}.justify-center{justify-content:center}.justify-evenly{justify-content:space-evenly}.space-around{justify-content:space-around}.align-center{align-items:center}.flex-start{justify-content:start}.m-auto{margin:0 auto}

input{width:80%;margin:0.5rem;border:none;padding:0.3rem;outline:none}.input-container{margin:2rem;width:40%;padding:0.5rem;box-shadow:0 8px 18px rgba(0,0,0,0.19),0 5px 5px rgba(0,0,0,0.23);border-radius:3px}.btn-conatiner{width:100%;margin-top:0.5rem}.btn{border-radius:3px;background-color:white;color:#323232;border:1px solid #323232}.btn:hover{cursor:pointer}

.card{overflow-wrap:break-word;border-radius:5px;display:inline-block;min-height:144px;margin:1rem;position:relative;padding:15px;width:238px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.card-title{margin-bottom:1rem}.card:hover{cursor:pointer;box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22)}.card-link{color:black}.red{background-color:#e03f3f}.blue{background-color:#6464c7}.green{background-color:#55c255}.yellow{background-color:#d5d542}

.noteDetails-container{background-color:#fff;position:absolute;top:25%;left:30%;padding:1.2rem;width:40%;height:30%;border-radius:5px;z-index:2;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.screen{z-index:1;background-color:rgba(235,235,235,0.884);position:fixed;top:0;left:0;right:0;bottom:0}.noteBody-container{height:80%}.noteIcons-container{height:20%}.noteIcons-container>*:not(:first-child){margin-left:1rem}.redbtn{width:20px;height:20px;border-radius:50%;background-color:red;margin:0.3rem}.bluebtn{width:20px;height:20px;border-radius:50%;background-color:blue;margin:0.3rem}.greenbtn{width:20px;height:20px;border-radius:50%;background-color:green;margin:0.3rem}.yellowbtn{width:20px;height:20px;border-radius:50%;background-color:yellow;margin:0.3rem}@media (max-width: 768px){.noteDetails-container{background-color:#fff;position:absolute;top:25%;left:10%;padding:1.2rem;width:70%;height:30%;border-radius:5px;z-index:2;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}}

.greet-text{background-color:white;color:#4c4c4c;width:100%;height:80vh;margin:0 auto;padding-top:5rem}.greet-text .box-1{width:50%;height:80%;padding:1rem;font-size:3rem}.greet-text .box-1 span{color:#f67e7e}.greet-text .box-1 img{height:15rem;position:relative;right:8rem}.greet-text .box-2{width:50%;height:100%;font-size:1.5rem;justify-content:space-between}.greet-text .box-2 p{max-height:5rem}.greet-text .box-2 p::before{content:"";display:block;width:4rem;height:0.2rem;margin-right:0.5rem;margin-bottom:8rem;background-color:#f67e7e}.greet-text .box-2 img{width:35rem}@media (max-width: 768px){.greet-text{flex-direction:column;padding-top:2rem}.greet-text .box-1{font-size:2rem;letter-spacing:0.4rem;margin:0 auto;width:100%;height:10rem}.greet-text .box-1 h4{text-align:center}.greet-text .box-1 img{display:none}.greet-text .box-2{width:90%;padding:1rem;font-size:1.2rem;text-align:center}.greet-text .box-2 p::before{display:none}.greet-text .box-2 img{display:none}}

.toggle-button{display:flex;flex-direction:column;justify-content:space-around;height:24px;width:30px;background:transparent;border:none;cursor:pointer;padding:0;box-sizing:border-box}.toggle-button:focus{outline:none}.toggle-button__line{width:30px;height:2px;background:#4e4e4e}

.toolbar{width:80%;background:white;height:10vh;margin:0 auto}.toolbar__navigation{display:flex;height:100%;align-items:center;padding:0 1rem}.toolbar__logo{margin-left:1rem}.toolbar__logo a{color:#4c4c4c;text-decoration:none;font-size:1.5rem}.spacer{flex:1 1}.toolbar_navigation-items ul{list-style:none;margin:0;padding:0;display:flex}.toolbar_navigation-items li{padding:0 0.5rem}.toolbar_navigation-items a{color:#4c4c4c;text-decoration:none}.toolbar_navigation-items a:hover,.toolbar_navigation-items a:active{color:#fa923f}@media (max-width: 768px){.toolbar{width:100%}.toolbar_navigation-items{display:none}.toolbar__logo{margin-left:2rem}}

.side-drawer{height:100%;background:white;box-shadow:1px 0px 7px rgba(0,0,0,0.5);position:fixed;top:0;left:0;width:70%;max-width:400px;z-index:200;transform:translateX(-100%);transition:transform 0.3s ease-out;text-align:center}.side-drawer.open{transform:translateX(0)}.side-drawer ul{padding:0;margin:1rem;height:100%;list-style:none}.side-drawer li{margin:1rem 0}.side-drawer a{color:#521751;text-decoration:none;font-size:1.2rem}.side-drawer a:hover,.side-drawer a:active{color:#fa923f}@media (min-width: 769px){.side-drawer{display:none}}

.backdrop{position:fixed;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.3);z-index:100}

